@value makerBoldGrey, makerLightGrey from '../../common/colors.css';
@value mediaQuery from '../../common/size.css';

.preview {
    flex-basis: 50%;
    overflow-y: auto;
}

.preview::-webkit-scrollbar {
    width: 10px;
    height: 10px;
}

.preview::-webkit-scrollbar-thumb {
    background-color: makerBoldGrey;
    border-radius: 10px;
    background-clip: padding-box;
    border: 2px solid transparent;
}
.preview::-webkit-scrollbar-track {
    background-color: grey;
    border-radius: 10px;
    box-shadow: inset 0px 0px 5px #fff;
}

.title {
    width: 100%;
    text-align: center;
    margin-bottom: 1em;
    color: makerBoldGrey;
}

.cards {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
}
